
.quotes {

  background-color: $quotes-bg-color;
  padding: 0 20px;

  &-container{
    padding: 30px 0;
    margin: 0 0 0 0%;
    text-align: center;
    display: table;
  }

  &__pane {
    display: table-cell;
    max-width: 644px;
    margin: 0 auto;
    padding: 0 0 0 25px;
    transition: 1s opacity ease-in-out;
    opacity: 0;
  }

  &__pane:first-child {
    visibility: visible;
  }

  &__detail {
    max-width: 644px;
    margin: 0 auto;
  }

  &__text, &__name {
    color: $quotes-color;
    font-size: mq-px2em(20);
    font-style: italic;
  }

  &__text {
    margin: 0 10px 10px 0;
  }

  /*&__text:before,*/
  .startquote:before,
  .endquote:before {
    font-size: mq-px2em(60);
    font-family: Georgia, serif;
  }

  .startquote:before {
    content: '\201C';
    position: absolute;
    margin-top: -26px;
    margin-left: -50px;
    width: 40px;
  }

  .endquote:before {
    content: '\201D';
    position: absolute;
    margin-top: -15px;
    padding-left: 5px;
    width: 40px;
  }

}
