
.content-wrapper {
  width: 100%;
  overflow: hidden;
  background: #fff;
  position: absolute;
}

.middle-container {
  margin-bottom: 60px;
}


.content {
  text-align: left;

  &--home, &--blogposts {
    text-align: center;
  }

  &__heading, &__subheading {
    margin: 0.5em auto;
    height: 1.5em;
    text-align: center;
  }

  &__heading {
    border-bottom: $global-header1-bottom-border-color solid 1px;
    max-width: 800px;
  }

  &__page-nav-list {
    border-left: 3px solid $global-pagenavlist-border-color;
    padding-left: 5px;
  }

  .page-nav-list__heading {
    margin: 0;
    font-weight: 400;
  }

  &__detail {
    max-width: 672px;
    margin: 0 auto 25px auto;
    font-size: mq-px2em(16);

    &--wide {
      max-width: 860px;
    }

    &--company{

      & span:first-child {
        font-weight: 400;
        display: block;

      }
      & span:nth-child(2) {
        font-style: italic;
        display: block;
        margin-bottom: 5px;
      }

    }

  }

  &__detail:last-child {
    margin-bottom: 65px;
  }

}
