
.totop {

  background-color: $totop-bg-color;
  text-align: center;
  position: fixed;
  bottom: 0;
  right: 5%;
  border-radius: 10px 10px 0 0;
  border-top: 1px solid $totop-border-color;
  border-left: 1px solid $totop-border-color;
  border-right: 1px solid $totop-border-color;
  transition: opacity 0.5s;

  &.js-totop {
    opacity: 1;
  }

  &:not(.js-totop){
    opacity: 0;
  }

  &__link {
    font-size: mq-px2em(15);
    color: $totop-text-color;
    padding: 5px;
    display: block;
  }

}
