

// clients list component
.clients {
  background-color: $clients-bg-color;
  padding-top: 30px;


  &__heading {
    text-transform: uppercase;
    font-size: mq-px2em(18);
    font-family: 'Source Sans Pro', sans-serif;
    color: $clients-color;
    text-align: center;
  }

  &__list {
    text-align: center;
    opacity: 1;
    margin: 0px 30px 30px 30px;
  }

  &__item {
    display: inline-block;
    vertical-align: middle;
    padding: 10px 40px;
  }

  @include mq($until: tablet){
    &__item {
      padding: 12px;
    }

    &__item{
      opacity: 0.8;
    }
  }

  @include mq($from: tablet) {
    &__item {
      transition: opacity 0.3s;
      opacity: 0.8;
    }

    &__item:hover {
      transition: opacity 0.3s;
      opacity: 1;
    }
  }

}
