
.posts {

  &__heading {
    color: #fff;
  }

  &__item {

    margin-bottom: 10px;
    border-bottom: 1px solid $footer-top-primary-color;
    padding-bottom: 10px;

    p {
      margin: 0;
      line-height: 1.5;
    }

    .item__desc {
      color: $footer-top-primary-color;
      font-size: mq-px2em(14);
    }
    .item__cat {
      color: $footer-top-secondary-color;
      font-size: mq-px2em(12);
    }

  }
}
