

// header component
.header {
  height: 380px;
  background-color: #0a0a0a;

  .search {
    top: 65px;
    position: absolute;
    width: 100%;
    text-align: center;

    &-wrapper {
      width: 100%;
      position: absolute;
      z-index: 200;
      margin-top: -78px;
    }

    @include mq($until: mobile) {
      &-wrapper{
        width: 80%;
      }
    }

    @include mq($until: tablet) {
      &-wrapper {
        width: 80%;
        text-align: center;
      }
    }

    &__border {
      height: 2px;
      width: 100%;
      background-color: #fff;
      opacity: 0.5;
    }

    &__bg {
      height: 100px;
      background-color: $header-search-bg-color;
      opacity: 0.8;
      /*border-bottom: #fff solid 2px;*/
    }

    &__input {
      width: 100%;
      border: none;
      background: none;
      color: #fff;
      font-size: mq-px2em(40);
      padding: 3px;
      text-align: center;
    }

    &__input:focus {
      outline: 0;
    }
  }

  .header-detail {
    position: absolute;
    top: 58%;
    left: 50%;
    width: 310px;
    transform: translate(-50%, -50%);

    .detail__photo {
      border-radius: 400%;
      border: 3px #fff solid;
    }

    .detail__desc {
      color: #fff;
      font-size: mq-px2em(18);
      margin: 10px 0 22px 0;
    }

    @media (max-device-height: 320px) and (orientation: landscape) {
      top: 45%;
    }

    /* modifier */
    &--blog-post {
      top: 52%;
      width: 90%;

      .detail__photo{
        width: 48px;
        margin-bottom: 10px;
      }

      .detail__desc {
        font-size: mq-px2em(48);
        font-weight: 600;
        text-transform: uppercase;
        line-height: 1;
        margin: 0 0 10px 0;
        height: 96px;
      }

      .detail__date {
        color: #fff;
        font-size: mq-px2em(16);
        margin: 0 0 5px 0;
      }

      .detail__readtime {
        color: #fff;
        font-size: mq-px2em(14);
        margin: 0 0 10px 0;
      }
      .detail__arrow {
        top: 104%
      }

      @include mq($until: tablet){
        .detail__date {
          font-size: mq-px2em(14);
        }
        .detail__readtime {
          font-size: mq-px2em(12);
        }
      }

      @include mq($until: tablet, $and: '(orientation: landscape)'){
        .detail__desc {
          font-size: mq-px2em(30);
        }
      }

      @include mq($until: tablet, $and: '(orientation: portrait)'){
        .detail__desc {
          font-size: mq-px2em(30);
        }
      }
      @include mq($from: wide, $and: '(orientation: landscape)') {
        .detail__desc {
          font-size: mq-px2em(50);
        }
      }

      /* using media query implicitly due to em calculation on max-width inaccurate */
      /* target iphone 5 */
      @media (max-device-width: 320px) and (orientation: portrait) {
        .detail__desc {
          font-size: mq-px2em(25);
        }
      }
      @media (max-device-height: 320px) and (orientation: landscape) {
        top: 45%;
        .detail__desc {
          height: 60px;
          font-size: mq-px2em(30);
        }
      }

    }

  }

  .detail__arrow {
    font-size: mq-px2em(24);
    color: #fff;
    opacity: 0.5;
    top: 106%;
    left: 50%;
    display: block;
    position: absolute;
    cursor: default;
    width: 30px;
    margin-left: -15px;
    text-decoration: none;
  }

  .hero {
    position: relative;
    text-align: center;
    width: 100%;
    overflow: hidden;
  }

  @media screen
  and (min-device-width: 1200px)
  and (max-device-width: 1600px)
  and (-webkit-min-device-pixel-ratio: 1),  screen
  and (min-device-width: 1200px)
  and (max-device-width: 1600px)
  and (-webkit-min-device-pixel-ratio: 2)
  and (min-resolution: 192dpi) {
    .hero {
      position: fixed;
    }
  }


  .hero-bg {
    background-position: center;
    background-size: cover;
    height: 380px;

    @include mq($from: tablet) {
      background-image: url('../images/header-backgrounds/header-home_large.jpg');
    }

    @include mq($from: mobile) {
      background-image: url('../images/header-backgrounds/header-home_small.jpg');
    }

  } /// .search


  &-overlay {
    background: #0a0a0a;
    top: 0;
    left: 0;
    height: 380px;
    width: 100%;
    opacity: 0.35;
    position: absolute;
  }

}

/* target iphone 5 */
@media (max-height: 320px) and (orientation: landscape) {
  .header {
    height: 320px;
  }
}
