
.footer-top {
  background-color: $footer-top-bg-color;
  min-height: 310px;
  padding: 40px 0;


  .tweets {
    border-right: 1px solid $footer-top-secondary-color;

    &__heading {
      color: $footer-top-heading-color;
    }

    &__item {
      color: #fff;
      overflow: hidden;
    }

    .item__desc {
      margin-top: 0;
    }

    @include mq($from: mobile, $until: desktop){
      & {
        border: none;
        border-bottom: 1px solid $footer-top-secondary-color;
        padding-bottom: 10px;
      }
    }

  }

}

.footer-bottom {
  background-color: $footer-bottom-bg-color;
  text-align: center;

  &__copy-text {
    color: $footer-bottom-copyright-color;
  }

  &__git-link {
    color: $footer-bottom-github-color;
  }

  &__git-link:hover {
    color: $footer-bottom-github-hover-color;
    cursor: pointer;
  }

}

