
.notify {

  font-size: mq-px2em(18);
  font-weight: 400;

  &--email {

  }

}

.btn {

  background: none;

  &--large {
    border-radius: 20px;
    padding: 10px 60px;
    font-size: mq-px2em(16);
  }

  &--primary {
    border: 1px solid $global-primary-button-color;
    color: $global-primary-button-color;
  }
  &--primary:hover {
    opacity: 0.5;
  }

  &--secondary {
    border: 1px solid $global-secondary-button-color;
    color: $global-secondary-button-color;
  }

  &--secondary:hover {
    opacity: 0.5;
  }

}
