
.site-nav {

  float: right;

  // possible layout? move?
  &-container {
    position: absolute;
    z-index: 100;
    width: 100%;
  }

  &-wrapper {
    box-shadow: none;
    padding: 10px 0px;

    &.is-dropdown {
      background-color: #fff;
      box-shadow: 0px 1px 5px $_color-timberwolf;
      box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.1);
      border-bottom: $_color-timberwolf 1px solid;
      position: fixed;
      width: 100%;

      & .site-nav__logo,
      & .site-nav__link,
      & .site-nav__search {
        color: #000;
      }
      & .site-nav__link:hover {
        border-bottom-color: #000;
      }
      & .mobile__burger span {
        background-color: #000;
      }

    }
  }

  &__logo {
    color: $header-logo-color;
    font-weight: 600;
    display: inline-block;
    font-size: mq-px2em(20);
    margin: 0;
  }

  @include mq($from: tablet){
    &--mobile {
      display: none;
    }
    &--desktop {
      display: block;
    }
  }
  @include mq($from: mobile, $until: tablet){
    &--mobile {
      display: block;
    }
    &--desktop {
      display: none;
    }
  }


  &--mobile {

    .mobile {

      &-overlay {
        height: 100%;
        width: 100%;
        z-index: 800;
        top: 0;
        left: 0;
        position: fixed;
        background: rgba(0,0,0,0.25);
        display: none;
      }

      &__items {
        background-color: $_color-dark-jungle-green;
        position: fixed;
        top: 0;
        right: -300px;
        width: 300px;
        height: 100%;
        z-index: 888;
        opacity: 0.95;
        overflow-y: auto;
        margin-top: 0;
        padding: 60px 50px 50px 50px;
        transition: 0.5s right ease-in-out;
      }

      &__items.is-open {
        right: 0;
      }

      &__item {
        font-family: 'Source Sans Pro', sans-serif;
        font-weight: 600;
        text-transform: uppercase;
        font-size: mq-px2em(16);
        margin-bottom: 20px;
      }

      &__link {
        color: #fff;
      }

      &__link:hover {
        text-decoration: none;
      }

      &__burger {
        display: block;
        float: right;
        position: absolute;
        top: 15px;
        right: 20px;
        z-index: 999;

        &.is-fixed {
          position: fixed;
        }

      }



      &__burger span {
        background-color: #fff;
        border-radius: 5px;
        height: 3px;
        display: block;
        width: 20px;
        transition: 0.25s all ease-in-out;
        position: absolute;
        right: 0px;
      }
      &__burger span:nth-child(1) {
        top: 0px;
      }
      &__burger span:nth-child(2) {
        top: 8px;
      }
      &__burger span:nth-child(3) {
        top: 16px;
        margin-bottom: 0;
      }


      &__burger.show-cross span {
        background-color: #fff;
      }
      &__burger.show-cross span:nth-child(1) {
        transform: rotate(45deg);
        top: 8px;
        right: 0px;
        position: absolute;
      }
      &__burger.show-cross span:nth-child(2) {
        width: 0%;
        opacity: 0;
        top: 8px;
        right:0px;
        position: absolute;
      }
      &__burger.show-cross span:nth-child(3) {
        transform: rotate(-45deg);
        top: 8px;
        right: 0px;
        position: absolute;
      }

    }

  }

  &__items {
    float: right;
    margin: 0;
  }

  &__item {
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 600;
    text-transform: uppercase;
    font-size: mq-px2em(14);
    display: inline-block;
    margin: 0 20px;
    vertical-align: middle;
    position: relative;
  }

  &__item:last-child {
    width: 30px;
    margin-right: 0px;
  }

  &__link {
    color: $site-nav-color;

    &--disabled {
      color: $site-nav-color-focus;
    }

  }

  &__link:hover {
    border-bottom: #fff solid 2px;
    text-decoration: none;
  }

  &__search {
    font-size: mq-px2em(20);
    color: #fff;

    &:hover {
      text-decoration: none;
    }

    &--close {

      width: 20px;
      height: 20px;
      display: inline-block;
      position: relative;
      top: 2px;

      span {
        display: block;
        position: absolute;
        height: 2px;
        width: 100%;
        top: 8px;
        background-color: #fff;
      }

      span:first-child {
        transform: rotate(135deg);
      }

      span:nth-child(2) {
        transform: rotate(45deg);
      }

    }

  }



}
