
.blog-posts {

  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;

  margin-top: -18px;

  &__item {
    min-width: 230px;

    margin: 30px 10px 15px 10px;

    width: 230px;
    height: 300px;

    & a:hover span {
      border: 1px solid #fff;
      color: #000;
      background-clip: text;
      background-color: #fff;
    }

    .item {
      &__image-wrapper {
        position: relative;
        height: 200px;
        width: 100%;
        overflow: hidden;
      }
      &__image {
        object-fit: cover;
        height: 230px;
        width: 100%;
      }
      &__readtime {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        color: #fff;
        font-size: mq-px2em(12);
        border: 1px solid #fff;
        border-radius: 10px;
        padding: 0px 10px;
      }
      &__date {
        font-size: mq-px2em(12);
        display: block;
        margin-top: 10px;
      }
      &__label-link {
        font-family: 'Source Sans Pro', sans-serif;
        font-size: mq-px2em(14);
        font-weight: bold;
        margin: 5px 5px 0 5px;
        color: $blogposts-heading-color;
        text-transform: uppercase;
        display: block;
      }
      &__label-link:hover {
        opacity: 0.6;
        text-decoration: none;
      }
    }
    &:last-child {
      margin-bottom: 30px;
    }
  }

  @include mq($from: mobile, $until: tablet) {
    &__item {
      width: 100%;
      margin-bottom: 0;

      .item {
        &__date {
          margin-top: 25px;
        }
      }
    }
  }

  @include mq($from: tablet, $until: desktop) {
    &__item {
      width: 45%;
    }
  }

}
