h1, h2 {
  color: $global-header1-color;
  font-family: 'Source Sans Pro', sans-serif;
}

h1 {
  font-weight: 200;
  font-size: mq-px2em(36);
}

h2 {
  font-weight: 200;
  font-size: mq-px2em(24);
}

h3 {
  font-family: 'Source Sans Pro', sans-serif;
  font-size: mq-px2em(20);
}

p {
  color: $global-text-color;
  /*font-family: 'Heebo', sans-serif;*/
  font-family: 'Yantramanav', sans-serif;
  font-weight: 100;
  line-height: 1.5;
  font-size: mq-px2em(16);
}

hr {
  border: 0;
  border-bottom: 1px solid $global-seperator-bottom-border-color;
}

ul {
  text-decoration: none;
  list-style-type: none;
  padding: 0;
}

ul li {
  padding: 0;
  margin: 0;
  font-family: 'Yantramanav', sans-serif;
}

button, input[type="submit"] {
  cursor: pointer;
  font-family: 'Source Sans Pro', sans-serif;
}

blockquote {
  font-family: 'Source Sans Pro', sans-serif;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: $global-link-color;
  font-weight: 400;

  &:hover {
    text-decoration: underline;
  }
}

/* form elements */
input:not([type="submit"]), textarea {
  padding: 10px;
  border: 0;
  border-bottom: 1px solid $_color-timberwolf;
  color: $_color-dark-jungle-green;
  font-size: mq-px2em(16px);
  outline: none;
}

textarea {
  padding: 10px;
  width: 100%;
}





