

.blog-post {

  padding-top: 20px;

  .aside-container {

    margin-top: 20px;

    .item__link {
      color: $_color-dark-jungle-green;
    }

    .aside__heading {
      font-weight: 600;
      font-size: mq-px2em(16);
      color: #000;
    }

    .posts__item {
      border-bottom: 1px solid $_color-timberwolf;
    }

    .reading {

      .desc-container {
        float: left;
        margin-left: 10px;
        width: 140px;
      }
      &__book {
        width: 76px;
        float: left;
      }
      &__name {
        font-size: mq-px2em(14);
        margin: 0;
      }
      &__desc {
        font-size: mq-px2em(12);
        margin: 0;
      }

    }

    .archive {

      &__heading {
        font-size: mq-px2em(14);
        font-weight: 400;
        color: $_color-quartz;
      }

    }

    .posts {
      &__list {
        &__item {
        }
        .item__desc {
          font-size: mq-px2em(16);
        }
        .item__cat {
          font-size: mq-px2em(14);
        }
      }
    }
  }

  &__article {

    p code {
      background-color: #f1f1f1;
      padding: 2px 5px 3px 5px;
      margin: 0 5px;
      font-size: 0.9em;
    }

    blockquote {
      border-left: 3px solid $blogpost-quote-border-color;
      padding-left: 20px;
      margin-left: 0;
      font-size: mq-px2em(14);
      font-weight: 400;
      font-style: italic;

      p {
        margin: 0;
      }

    }

    pre {
      max-height: 300px;
    }

  }

}
