
.twitter-timeline, .timeline-Tweet-text {
  visibility: hidden;
}

.tweets {

  &__item {

    margin-bottom: 10px;

    .fa-twitter {
      color: $footer-top-twitter-color;
      float: left;
    }

    p {
      margin: 5px 0 0 24px;
    }

    .item__desc {
      color: $footer-top-primary-color;
      font-size: mq-px2em(14);

      a {
        color: $footer-top-link-color;
      }

      a:hover {
        color: $footer-top-link-hover-color;
      }

    }

    .item__date {
      color: $footer-top-secondary-color;
      font-size: mq-px2em(12);
    }

  }

}
